import { Options } from 'execa';
import { ExecaInterface } from './execa.interface';

export class ExecaWrapper implements ExecaInterface {
  private execa: any;

  constructor() {
    const execa = import('execa');
    execa
      .then((execa) => {
        this.execa = execa.execa;
      })
      .catch((error) => {
        console.log(error);
      });
  }

  async execute(
    command: string,
    args: string | Array<string>,
    workingDir: Options,
  ) {
    return this.execa(command, args, workingDir);
  }
}
